import React, { useEffect, useState, useCallback } from "react"
import Layout from "../component/layout"
import { css } from "@emotion/core"
import Helmet from "react-helmet"
import ISSO from "../component/comment"
import deleteIcon from '../image/delete.svg'
import debounce from 'lodash.debounce'

const hStyle = css`
  font-size: 1.6rem;
  text-align: center;
  margin: 1rem 0;
  @media (min-width: 500px) {
    font-size: 2rem;
    margin: 2rem 0 1rem 0;
  }
`

const thumbnailStyle = css`
  margin: 30px 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, 50px);
  grid-gap: 15px;
  div {
      display: flex;
      width: 50px;
      height: 50px;
  }
  .image-thumbnail {
      object-fit: cover;
      width: 50px;
      height: 50px;
      box-shadow: 0 0 3px grey;
  }
  .delete-icon {
      width: 20px;
      height: 20px;
      transform: translate(-10px, -10px);
  }
`

const buttonsStyle = css`
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 50px 0;
  place-items: center;
`

const chooseImageBtnStyle = css`
  #input-file {
    position: absolute;
    visibility:hidden;
    width: 0px;
    height: 0px;
  }
`

const imageBtnStyle = css`
    font-size: 20px;
    font-weight: bold;
    box-sizing: border-box;
    border: 1px solid grey;
    padding: 4px 8px;
    background-color: lightcyan;
    box-shadow: 0 0 3px grey;
    text-decoration: none;
    color: black;
    display: block;
    text-align: center;
    :active {
        color: red;
        text-decoration: none;
    }
`

const canvasContainerStyle = css`
    width: 100%;
    max-height: 1000px;
    overflow: scroll;
    padding: 5px;
    display: flex;
`

const canvasStyle = css`
  display: block;
  box-shadow: 0 0 3px grey;
  margin: auto;
`

const imageInfo = css`
    margin: 5px 0 50px 0;
    text-align: center;
`

const formStyle = css`
    .form-radio {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
        p {
            font-size: 18px;
            font-weight: bold;
            margin-bottom: 10px;
            grid-column: 1 / -1;
        }
        label {
            font-size: 16px;
        }
        label input {
            margin-right: 5px;
            font-size: 16px;
        }
    }
    .form-text-input {
        p {
            font-size: 18px;
            font-weight: bold;
            margin-bottom: 10px;
        }
        input {
            padding: 2px 4px;
            font-size: 16px;
            width: 100%;
            max-width: 300px;
        }
    }
    .form-color {
        p {
            font-size: 18px;
            font-weight: bold;
            margin-bottom: 10px;
        }
        input {
            margin-right: 10px;
        }
    }
    .form-slider {
        p {
            font-size: 18px;
            font-weight: bold;
            margin-bottom: 10px;
        }
        label {
            vertical-align: middle;
        }
        input {
            font-size: 16px;
            width: 80%;
            max-width: 300px;
            margin-right: 10px;
        }
    }
`

export default () => {
    const [images, setImages] = useState([]);
    const [thumbnails, setThumbnails] = useState([]);
    const [setting, setSetting] = useState({
        direction: 'vertical',
        widthSetting: 'equalWidth',
        alignVertical: 'alignCenterVertical',
        heightSetting: 'equalHeight',
        alignHorizontal: 'alignCenterHorizontal',
        watermark: '',
        watermarkColor: '#e66465',
        backgroundColor: '#ffffff',
        margin: 10,
        opacity: 0.3,
        fontSize: 50,
    });
    const [margin, setMargin] = useState(setting.margin);
    const [opacity, setOpacity] = useState(setting.opacity);
    const [fontSize, setFontSize] = useState(setting.fontSize);
    const [outputImageSize, setOutputImageSize] = useState({
        width: 0,
        height: 0,
    });

    var elementBeingDragged = '';

    function mergeImages(images, setting) {
        const dpr = window.devicePixelRatio;
        const {
            direction,
            widthSetting,
            alignVertical,
            heightSetting,
            alignHorizontal,
            watermark,
            watermarkColor,
            backgroundColor,
            margin,
            opacity,
            fontSize
        } = setting;
        const canvas = document.getElementById('canvas');
        const ctx = canvas.getContext('2d');
        const imagesCompressSize = images.map(image => {
            const imageSizeCompress = (() => {
                var width = image.width;
                var height = image.height;
                if (image.width > image.height && image.height > 2000) {
                    height = 2000;
                    width = image.width / image.height * 2000;
                };
                if (image.height > image.width && image.width > 2000) {
                    width = 2000;
                    height = image.height / image.width * 2000;
                };
                return {
                    width,
                    height
                }
            })();
            return {
                url: image.url,
                width: image.width,
                height: image.height,
                widthCompressed: imageSizeCompress.width,
                heightCompressed: imageSizeCompress.height,
            }
        });
        const largestWidth = Math.max(...imagesCompressSize.map(image => image.widthCompressed));
        const largestHeight = Math.max(...imagesCompressSize.map(image => image.heightCompressed));
        const imagesNew = imagesCompressSize.map(image => {
            var widthEqual = image.widthCompressed;
            var heightWhenWidthEqual = image.heightCompressed;
            var heightEqual = image.heightCompressed;
            var widthWhenHeightEqual = image.widthCompressed;
            if (widthSetting === 'equalWidth') {
                widthEqual = largestWidth;
                heightWhenWidthEqual = image.height / image.width * widthEqual;
            };
            if (heightSetting === 'equalHeight') {
                heightEqual = largestHeight;
                widthWhenHeightEqual = image.width / image.height * heightEqual;
            };
            return {
                url: image.url,
                width: image.width,
                height: image.height,
                widthEqual: widthEqual,
                heightWhenWidthEqual: Math.round(heightWhenWidthEqual),
                heightEqual: heightEqual,
                widthWhenHeightEqual: Math.round(widthWhenHeightEqual),
            }
        });
        console.log('imagesNew:', imagesNew)
        const totalWidth = imagesNew.reduce((a, b) => a + b.widthWhenHeightEqual, 0);
        const totalWidthAddMargin = totalWidth + margin * (imagesNew.length - 1);
        const totalHeight = imagesNew.reduce((a, b) => a + b.heightWhenWidthEqual, 0);
        const totalHeightAddMargin = totalHeight + margin * (imagesNew.length - 1);
        if (direction === 'vertical') {
            canvas.width = largestWidth;
            canvas.height = totalHeightAddMargin;
            if (Math.round(largestWidth / dpr) > 600) {
                canvas.style.width = '600px';
                canvas.style.height = `${canvas.height / canvas.width * 600}px`
            } else {
                canvas.style.width = `${Math.round(largestWidth / dpr)}px`;
                canvas.style.height = `${canvas.height / canvas.width * Math.round(largestWidth / dpr)}px`
            }
        } else {
            canvas.width = totalWidthAddMargin;
            canvas.height = largestHeight;
            if (Math.round(largestHeight / dpr) > 800) {
                canvas.style.height = '800px';
                canvas.style.width = `${canvas.width / canvas.height * 800}px`
            } else {
                canvas.style.height = `${Math.round(largestHeight / dpr)}px`;
                canvas.style.width = `${canvas.width / canvas.height * Math.round(largestHeight / dpr)}px`
            }
        };
        setOutputImageSize({
            width: canvas.width,
            height: canvas.height
        })
        ctx.save();
        ctx.fillStyle = backgroundColor;
        ctx.fillRect(0, 0, canvas.width, canvas.height);
        ctx.restore();
        const drawImage = function (image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight, index) {
            const img = new Image();
            img.src = image.url;
            img.onload = async () => {
                console.log('drawImage的参数:', img, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight, index)
                ctx.drawImage(img, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight);
                if (index === imagesNew.length - 1) {
                    if (watermark && imagesNew.length) {
                        console.log('开始加水印')
                        ctx.save();
                        ctx.fillStyle = hexToRgbA(watermarkColor).replace(/,1\)/g, `,${opacity})`);
                        ctx.textAlign = 'center';
                        ctx.textBaseline = 'middle';
                        ctx.font = `${fontSize}px serif`;
                        ctx.translate(Math.round(canvas.width / 2), Math.round(canvas.height / 2));
                        ctx.rotate(45 * Math.PI / 180);
                        const length = Math.sqrt(canvas.width / 2 * canvas.width / 2 + canvas.height / 2 * canvas.height / 2);
                        const textWidth = ctx.measureText(watermark).width;
                        for (let x = 0; x < length; x = x + textWidth * 1 + fontSize * 2) {
                            for (let y = 0; y < length; y = y + fontSize * 3) {
                                if (x === 0 && y === 0) {
                                    ctx.fillText(watermark, x, y, length);
                                } else if (x === 0 && y !== 0) {
                                    ctx.fillText(watermark, x, y, length);
                                    ctx.fillText(watermark, x, -y, length);
                                } else if (x !== 0 && y === 0) {
                                    ctx.fillText(watermark, x, y, length);
                                    ctx.fillText(watermark, -x, y, length);
                                } else {
                                    ctx.fillText(watermark, x, y, length);
                                    ctx.fillText(watermark, -x, y, length);
                                    ctx.fillText(watermark, x, -y, length);
                                    ctx.fillText(watermark, -x, -y, length);
                                }
                            }
                        }
                        ctx.restore();
                    }
                }
            }
        };
        for (let [index, image] of imagesNew.entries()) {
            const sx = 0;
            const sy = 0;
            const sWidth = image.width;
            const sHeight = image.height;
            if (direction === 'vertical') {
                if (widthSetting === 'equalWidth') {
                    const dx = 0;
                    const dy = imagesNew.slice(0, index).reduce((a, b) => a + b.heightWhenWidthEqual, 0) + margin * index;
                    const dWidth = image.widthEqual;
                    const dHeight = image.heightWhenWidthEqual;
                    drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight, index);
                } else {
                    if (alignVertical === 'alignLeft') {
                        const dx = 0;
                        const dy = imagesNew.slice(0, index).reduce((a, b) => a + b.heightEqual, 0) + margin * index;
                        const dWidth = image.widthEqual;
                        const dHeight = image.heightWhenWidthEqual;
                        drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight, index);
                    } else if (alignVertical === 'alignCenterVertical') {
                        const dx = Math.round((largestWidth - image.width) / 2);
                        const dy = imagesNew.slice(0, index).reduce((a, b) => a + b.heightEqual, 0) + margin * index;
                        const dWidth = image.widthEqual;
                        const dHeight = image.heightWhenWidthEqual;
                        drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight, index);
                    } else if (alignVertical === 'alignRight') {
                        const dx = largestWidth - image.width;
                        const dy = imagesNew.slice(0, index).reduce((a, b) => a + b.heightEqual, 0) + margin * index;
                        const dWidth = image.widthEqual;
                        const dHeight = image.heightWhenWidthEqual;
                        drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight, index);
                    }
                }
            } else {
                if (heightSetting === 'equalHeight') {
                    const dy = 0;
                    const dx = imagesNew.slice(0, index).reduce((a, b) => a + b.widthWhenHeightEqual, 0) + margin * index;
                    const dWidth = image.widthWhenHeightEqual;
                    const dHeight = image.heightEqual;
                    drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight, index);
                } else {
                    if (alignHorizontal === 'alignTop') {
                        const dy = 0;
                        const dx = imagesNew.slice(0, index).reduce((a, b) => a + b.widthEqual, 0) + margin * index;
                        const dWidth = image.widthWhenHeightEqual;
                        const dHeight = image.heightEqual;
                        drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight, index);
                    } else if (alignHorizontal === 'alignCenterHorizontal') {
                        const dy = Math.round((largestHeight - image.height) / 2);
                        const dx = imagesNew.slice(0, index).reduce((a, b) => a + b.widthEqual, 0) + margin * index;
                        const dWidth = image.width;
                        const dHeight = image.height;
                        drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight, index);
                    } else if (alignHorizontal === 'alignBottom') {
                        const dy = largestHeight - image.height;
                        const dx = imagesNew.slice(0, index).reduce((a, b) => a + b.widthEqual, 0) + margin * index;
                        const dWidth = image.widthWhenHeightEqual;
                        const dHeight = image.heightEqual;
                        drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight, index);
                    }
                }
            };
        };
    };

    async function getThumbnail(images) {
        function getOneThumbnail(image) {
            return new Promise((resolve, reject) => {
                const canvas = document.createElement('canvas');
                const ctx = canvas.getContext('2d');
                canvas.width = 100;
                canvas.height = 100;
                const sWidth = Math.min(image.height, image.width);
                const sHeight = Math.min(image.height, image.width);
                const img = new Image();
                img.src = image.url;
                img.onload = () => {
                    ctx.fillStyle = 'white';
                    ctx.fillRect(0, 0, 100, 100);
                    ctx.drawImage(img, 0, 0, sWidth, sHeight, 0, 0, 100, 100);
                    const thumbnail = canvas.toDataURL('image/jpeg');
                    resolve(thumbnail);
                    reject('get thumbnail 出错')
                }
            })
        };
        const promiseArray = images.map(image => getOneThumbnail(image));
        return await Promise.all(promiseArray);
    }

    function hexToRgbA(hex) {
        var c;
        if (/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)) {
            c = hex.substring(1).split('');
            if (c.length === 3) {
                c = [c[0], c[0], c[1], c[1], c[2], c[2]];
            }
            c = '0x' + c.join('');
            return 'rgba(' + [(c >> 16) & 255, (c >> 8) & 255, c & 255].join(',') + ',1)';
        }
        throw new Error('Bad Hex');
    }

    useEffect(() => {
        (async () => {
            setThumbnails(await getThumbnail(images))
        })()
    }, [images]);

    useEffect(() => {
        const canvas = document.getElementById('canvas');
        if (images.length) {
            mergeImages(images, setting);
            canvas.style.display = '';
        } else {
            canvas.style.display = 'none';
        }
    }, [images, setting]);

    const onSelectImages = async (e) => {
        const files = [...e.target.files].map(image => URL.createObjectURL(image));
        console.log('files:', files)
        const getImageSize = (imageUrl) => {
            return new Promise((resolve, reject) => {
                const img = new Image();
                img.src = imageUrl;
                img.onload = () => {
                    resolve({
                        width: +img.width,
                        height: +img.height,
                        url: img.src
                    });
                    reject('出错')
                }
            })
        };
        const imagesWithSizeInfo = await Promise.all(files.map(file => getImageSize(file)));
        setImages(imagesWithSizeInfo)
    };

    const deleteImage = function (e) {
        const index = e.target.dataset.index;
        const imagesTemp = JSON.parse(JSON.stringify(images))
        imagesTemp.splice(index, 1)
        setImages(imagesTemp)
    }

    const saveImage = function () {
        const canvas = document.getElementById('canvas');
        const a = document.getElementById('download');
        if (canvas.width === 0) {
            a.removeAttribute('href')
        } else {
            const outputImage = canvas.toDataURL('image/jpeg');
            a.href = outputImage;
        }
    };

    const onDrag = function (e) {
        elementBeingDragged = e.target;
    }

    const onDrop = function (e) {
        const elementBeingSwap = e.target;
        elementBeingSwap.style.border = 'none';
        if (elementBeingSwap.src === elementBeingDragged.src) return;
        const imagesTemp = JSON.parse(JSON.stringify(images));
        const index_1 = imagesTemp.map(image => image.url).indexOf(elementBeingSwap.src)
        const index_2 = imagesTemp.map(image => image.url).indexOf(elementBeingDragged.src)
        const tempElement = imagesTemp[index_1];
        imagesTemp[index_1] = imagesTemp[index_2];
        imagesTemp[index_2] = tempElement;
        setImages(imagesTemp);
    }

    const onDragOver = function (e) {
        e.preventDefault();
    }

    const onDragEnter = function (e) {
        e.target.style.border = '1px solid red'
    }

    const onDragLeave = function (e) {
        e.target.style.border = 'none';
    }

    const changeForm = function (e) {
        const settingName = e.target.name;
        const settingValue = e.target.value;
        if (!['margin', 'fontSize', 'opacity'].includes(settingName)) {
            const settingTemp = JSON.parse(JSON.stringify(setting));
            settingTemp[settingName] = settingValue;
            setSetting(settingTemp)
        }
    }

    const debounceChangeSlider = useCallback(debounce((value, name) => {
        const settingTemp = JSON.parse(JSON.stringify(setting));
        settingTemp[name] = value;
        setSetting(settingTemp)
    }, 250), [setting]);

    const onChangeSlider = function (e) {
        const value = e.target.value;
        const name = e.target.name;
        switch (name) {
            case 'margin': {
                setMargin(value);
                break;
            }
            case 'fontSize': {
                setFontSize(value);
                break;
            }
            case 'opacity': {
                setOpacity(value);
                break;
            }
            default: {
                break;
            }
        };
        debounceChangeSlider(value, name)
    }

    return (
        <Layout>
            <Helmet>
                <title>图片拼接 | 长图拼接</title>
                <meta
                    name="description"
                    content="图片拼接, 把多张图片合并成一张, 支持纵向或横向拼接, 可以把多张图片拼接成一张长图片, 还能添加水印."
                />
            </Helmet>
            <h1 css={hStyle}>图片拼接 | 长图拼接</h1>
            <section>
                <form onChange={changeForm} css={formStyle} >
                    <div className="form-radio" >
                        <p>拼接方向：</p>
                        <label htmlFor="vertical">
                            <input type="radio" id="vertical"
                                name="direction" defaultValue="vertical" defaultChecked={setting.direction === 'vertical'} />
                            纵向拼接
                            </label>
                        <label htmlFor="horizontal">
                            <input type="radio" id="horizontal"
                                name="direction" defaultValue="horizontal" defaultChecked={setting.direction === 'horizontal'} />
                                横向拼接
                                </label>
                    </div>
                    {
                        setting.direction === 'vertical' ? (
                            <div className="form-radio" >
                                <p>宽度设置：</p>

                                <label htmlFor="equalWidth">
                                    <input type="radio" id="equalWidth"
                                        name="widthSetting" defaultValue="equalWidth" defaultChecked={setting.widthSetting === 'equalWidth'} />
                                    等宽
                                    </label>
                                <label htmlFor="originalWidth">
                                    <input type="radio" id="originalWidth"
                                        name="widthSetting" defaultValue="originalWidth" defaultChecked={setting.widthSetting === 'originalWidth'} />
                                    原始宽度
                                    </label>
                            </div>
                        ) : (
                                <div className="form-radio" >
                                    <p>高度设置：</p>
                                    <label htmlFor="equalHeight">
                                        <input type="radio" id="equalHeight"
                                            name="heightSetting" defaultValue="equalHeight" defaultChecked={setting.heightSetting === 'equalHeight'} />
                                        等高
                                        </label>
                                    <label htmlFor="originalHeight">
                                        <input type="radio" id="originalHeight"
                                            name="heightSetting" defaultValue="originalHeight" defaultChecked={setting.heightSetting === 'originalHeight'} />
                                        原始高度
                                        </label>
                                </div>
                            )
                    }
                    {
                        setting.direction === 'vertical' ? (
                            <div className="form-radio" >
                                <p>对齐方式：</p>
                                <label htmlFor="alignLeft">
                                    <input type="radio" id="alignLeft"
                                        name="alignVertical" defaultValue="alignLeft" defaultChecked={setting.alignVertical === 'alignLeft'} disabled={setting.widthSetting === 'equalWidth'} />
                                    左对齐
                                    </label>
                                <label htmlFor="alignCenterVertical">
                                    <input type="radio" id="alignCenterVertical"
                                        name="alignVertical" defaultValue="alignCenterVertical" defaultChecked={setting.alignVertical === 'alignCenterVertical'} disabled={setting.widthSetting === 'equalWidth'} />
                                    左右居中
                                    </label>
                                <label htmlFor="alignRight">
                                    <input type="radio" id="alignRight"
                                        name="alignVertical" defaultValue="alignRight" defaultChecked={setting.alignVertical === 'alignRight'} disabled={setting.widthSetting === 'equalWidth'} />
                                    右对齐
                                    </label>
                            </div>
                        ) : setting.direction === 'horizontal' ? (
                            <div className="form-radio" >
                                <p>对齐方式：</p>
                                <label htmlFor="alignTop">
                                    <input type="radio" id="alignTop"
                                        name="alignHorizontal" defaultValue="alignTop" defaultChecked={setting.alignHorizontal === 'alignTop'} disabled={setting.heightSetting === 'equalHeight'} />
                                    上对齐
                                    </label>
                                <label htmlFor="alignCenterHorizontal">
                                    <input type="radio" id="alignCenterHorizontal"
                                        name="alignHorizontal" defaultValue="alignCenterHorizontal" defaultChecked={setting.alignHorizontal === 'alignCenterHorizontal'} disabled={setting.heightSetting === 'equalHeight'} />
                                    上下居中
                                    </label>
                                <label htmlFor="alignBottom">
                                    <input type="radio" id="alignBottom"
                                        name="alignHorizontal" defaultValue="alignBottom" defaultChecked={setting.alignHorizontal === 'alignBottom'} disabled={setting.heightSetting === 'equalHeight'} />
                                    下对齐
                                    </label>
                            </div>
                        ) : ''
                    }
                    <div className="form-radio" >
                        <p>背景颜色：</p>
                        <label htmlFor="white">
                            <input type="radio" id="white"
                                name="backgroundColor" defaultValue="#ffffff" defaultChecked={setting.backgroundColor === '#ffffff'} />
                                    白色
                                    </label>
                        <label htmlFor="grey">
                            <input type="radio" id="grey"
                                name="backgroundColor" defaultValue="#808080" defaultChecked={setting.backgroundColor === '#808080'} />
                                    灰色
                                    </label>
                        <label htmlFor="black">
                            <input type="radio" id="black"
                                name="backgroundColor" defaultValue="#000000" defaultChecked={setting.backgroundColor === '#000000'} />
                                    黑色
                                    </label>
                    </div>
                    <div className="form-slider" >
                        <p>图片间距：</p>
                        <label htmlFor="margin">
                            <input type="range" id="margin" name="margin" min="0" max="500" defaultValue={margin} onChange={onChangeSlider} />
                            {margin}px
                            </label>
                    </div>
                    <div className="form-text-input" >
                        <p>添加水印：</p>
                        <input type="text" id="watermark" name="watermark" placeholder="请输入要显示的水印..." defaultValue={setting.water} />
                    </div>
                    {
                        setting.watermark ? (
                            <>
                                <div className="form-color" >
                                    <p>水印颜色：</p>
                                    <label htmlFor="watermarkColor">
                                        <input type="color" id="watermarkColor" name="watermarkColor" defaultValue={setting.watermarkColor} />
                                        {setting.watermarkColor}
                                    </label>
                                </div>
                                <div className="form-slider" >
                                    <p>透明度：</p>
                                    <label htmlFor="opacity">
                                        <input type="range" id="opacity" name="opacity" min="0" max="1" step="0.1" defaultValue={opacity} onChange={onChangeSlider} />
                                        {opacity}
                                    </label>
                                </div>
                                <div className="form-slider" >
                                    <p>水印大小：</p>
                                    <label htmlFor="fontSize">
                                        <input type="range" id="fontSize" name="fontSize" min="1" max="500" defaultValue={fontSize} onChange={onChangeSlider} />
                                        {fontSize}px
                                </label>
                                </div>
                            </>
                        ) : ''
                    }
                </form>
            </section>
            <section>
                <div css={buttonsStyle}>
                    <div css={chooseImageBtnStyle}>
                        <label htmlFor="input-file" css={imageBtnStyle}>选择图片</label>
                        <input id="input-file" type="file" onChange={onSelectImages} multiple accept="image/*" onClick={e => {
                            e.target.value = null
                        }}></input>
                    </div>
                    <a download onClick={saveImage} id="download" href="https://kejiweixun.com/merge-images/" css={imageBtnStyle} role="button">保存图片</a>
                </div>
                <div css={thumbnailStyle}>
                    {
                        thumbnails.map((thumbnail, index) => {
                            return (
                                <div key={thumbnail + Math.random()} draggable="true" onDragStart={onDrag} onDrop={onDrop} onDragOver={onDragOver} onDragEnter={onDragEnter} onDragLeave={onDragLeave}>
                                    <img src={thumbnail} className="image-thumbnail" alt="thumbnail"></img>
                                    <img src={deleteIcon} className="delete-icon" onClick={deleteImage} data-index={index} alt="delete icon"></img>
                                </div>
                            )
                        })
                    }
                </div>
                <div css={canvasContainerStyle}>
                    <canvas id="canvas" css={canvasStyle} width="0px" height="0px" ></canvas>
                </div>
                {
                    images.length ? (
                        <div css={imageInfo}>分辨率: {outputImageSize.width}px * {outputImageSize.height}px</div>
                    ) : ''
                }
            </section>
            <ISSO title="图片拼接" slug={`merge-images/`} />
        </Layout >
    )
}